import React, { Component } from "react";
import {
  Icon,
  Modal,
  Image,
  Form,
  Header,
  Button,
  Card,
  Grid,
  Dimmer,
  Loader
} from "semantic-ui-react";
import Globals from "../Globals";
import Axios from "axios";

const initState = {
  modalHotel: false,
  modalSubmitSuccess: false,
  selectedHotelId: 0,
  fullname: "",
  email: "",
  phone: "",
  submitting: false
};

class HotelList extends Component {
  state = initState;

  showModalHotel = id => {
    this.setState({
      modalHotel: true,
      selectedHotelId: id
    });
  };
  hideModalHotel = () => this.setState(initState);

  hotelCard = ({ id, img, url, name, stars, location, price }) => {
    var renderStars = [];
    for (var i = 0; i < stars; i++) {
      renderStars.push(
        <img
          key={i + 1}
          alt=""
          src="https://png.icons8.com/color/50/000000/filled-star.png"
          style={{ height: 25, width: "auto", marginRight: 3 }}
        />
      );
    }
    return (
      <Card
        fluid
        className="app-clickable"
        onClick={this.showModalHotel.bind(this, id)}
      >
        <Card.Content>
          <div style={{ display: "flex" }}>
            <img
              alt=""
              src={img}
              style={{ height: 150, width: 230, objectFit: "cover" }}
            />
            <div style={{ marginLeft: 10 }}>
              <span
                style={{ fontSize: 20, fontWeight: "bold", marginBottom: 12 }}
              >
                {name}
              </span>
              <div
                style={{ display: "flex", marginLeft: -2, marginBottom: 15 }}
              >
                {renderStars}
              </div>
              <div style={{ display: "flex" }}>
                <img
                  alt=""
                  src="https://png.icons8.com/metro/50/666666/marker.png"
                  style={{
                    height: 17,
                    width: "auto",
                    marginRight: 0,
                    marginTop: 3
                  }}
                />
                <p style={{ colod: "grey" }}>{location}</p>
              </div>
              <div style={{ marginLeft: 3, marginTop: 12 }}>
                <h3 style={{ color: Globals.COLOR_ORANYE }}>{`Rp ${price}`}</h3>
              </div>
            </div>
          </div>
        </Card.Content>
      </Card>
    );
  };

  onChangeForm = e => {
    var value = e.target.value;
    var name = e.target.name;
    this.setState({
      [name]: value
    });
  };

  bookNow = () => {
    // check if form empty
    if (
      this.state.fullname === "" ||
      this.state.email === "" ||
      this.state.phone === ""
    ) {
      alert("Fields cannot be empty!");
      return false;
    }

    this.setState({ submitting: true });

    // console.log(this.state.fullname);
    // console.log(this.state.email);
    // console.log(this.state.phone);

    var hotel_name = hotels.find(item => item.id === this.state.selectedHotelId)
      .name;
    var price = hotels.find(item => item.id === this.state.selectedHotelId)
      .price;
    var price_usd = hotels.find(item => item.id === this.state.selectedHotelId)
      .price_usd;

    var url = "https://rommyarbantas.net/submitBookHotelAres.php";
    var data = new FormData();
    data.append("fullname", this.state.fullname);
    data.append("email", this.state.email);
    data.append("phone", this.state.phone);
    data.append("hotel_name", hotel_name);
    data.append("price", price);
    data.append("price_usd", price_usd);
    Axios.post(url, data)
      .then(r => {
        if (r.data.success) {
          this.setState({
            modalSubmitSuccess: true,
            submitting: false
          });
        } else {
          console.log(r.data);
        }
      })
      .catch(e => {
        this.setState({
          submitting: false
        });
      });
  };

  render() {
    let selectedHotelId = this.state.selectedHotelId;
    let img =
      hotels.find(item => item.id === selectedHotelId) !== undefined
        ? hotels.find(item => item.id === this.state.selectedHotelId).img
        : null;
    let url =
      hotels.find(item => item.id === selectedHotelId) !== undefined
        ? hotels.find(item => item.id === this.state.selectedHotelId).url
        : null;
    let name =
      hotels.find(item => item.id === selectedHotelId) !== undefined
        ? hotels.find(item => item.id === this.state.selectedHotelId).name
        : null;
    let stars =
      hotels.find(item => item.id === selectedHotelId) !== undefined
        ? hotels.find(item => item.id === this.state.selectedHotelId).stars
        : null;
    let location =
      hotels.find(item => item.id === selectedHotelId) !== undefined
        ? hotels.find(item => item.id === this.state.selectedHotelId).location
        : null;
    let price =
      hotels.find(item => item.id === selectedHotelId) !== undefined
        ? hotels.find(item => item.id === this.state.selectedHotelId).price
        : null;

    var renderStars = [];
    for (var i = 0; i < stars; i++) {
      renderStars.push(
        <img
          alt=""
          src="https://png.icons8.com/color/50/000000/filled-star.png"
          style={{ height: 25, width: "auto", marginRight: 3 }}
        />
      );
    }
    return (
      <React.Fragment>
        {hotels.map((row, i) => (
          <this.hotelCard
            key={i}
            id={row.id}
            img={row.img}
            url={row.url}
            name={row.name}
            stars={row.stars}
            location={row.location}
            price={row.price}
          />
        ))}
        <Modal
          size="fullscreen"
          open={this.state.modalHotel}
          onClose={this.hideModalHotel}
        >
          <Modal.Content image>
            <Image wrapped size="large" src={img} />
            <Modal.Description>
              <div>
                <span
                  style={{
                    fontSize: 20,
                    fontWeight: "bold",
                    marginBottom: 12
                  }}
                >
                  {name}
                </span>
                <div
                  style={{
                    display: "flex",
                    marginLeft: -2,
                    marginBottom: 15
                  }}
                >
                  {renderStars}
                </div>
                <div style={{ display: "flex" }}>
                  <img
                    alt=""
                    src="https://png.icons8.com/metro/50/666666/marker.png"
                    style={{
                      height: 17,
                      width: "auto",
                      marginRight: 0,
                      marginTop: 3
                    }}
                  />
                  <p style={{ colod: "grey" }}>{location}</p>
                </div>
                <div style={{ marginLeft: 3, marginTop: 12 }}>
                  <h3
                    style={{ color: Globals.COLOR_ORANYE }}
                  >{`Rp ${price}`}</h3>
                </div>
              </div>
              <br />
              {/* /// form di sini /// */}
              <hr />
              <h4>BOOK NOW</h4>
              <Form>
                <Form.Field>
                  <label>Fullname</label>
                  <input
                    name="fullname"
                    onChange={this.onChangeForm}
                    placeholder="Fullname"
                    value={this.state.fullname}
                  />
                </Form.Field>
                <Form.Field>
                  <label>Email address</label>
                  <input
                    name="email"
                    onChange={this.onChangeForm}
                    type="email"
                    placeholder="Email address"
                    value={this.state.email}
                  />
                </Form.Field>
                <Form.Field>
                  <label>Phone number</label>
                  <input
                    name="phone"
                    onChange={this.onChangeForm}
                    placeholder="Phone number"
                    value={this.state.phone}
                  />
                </Form.Field>
                <Card fluid color="green">
                  <Card.Content>
                    <Card.Header>Price details</Card.Header>
                    <Grid columns={2}>
                      <Grid.Row>
                        <Grid.Column>(1x) Room</Grid.Column>
                        <Grid.Column>{`Rp ${price}`}</Grid.Column>
                      </Grid.Row>
                    </Grid>
                    <hr />
                    <b>Payment options:</b>
                    <br />
                    <br />
                    <Grid columns={2}>
                      <Grid.Row>
                        <Grid.Column>Transfer Bank BCA</Grid.Column>
                        <Grid.Column>
                          <img
                            alt=""
                            width="auto"
                            height={20}
                            src="https://ecs7.tokopedia.net/img/toppay/sprites/bca.png"
                          />
                          {/* <p>
                            BCA Cabang Cakranegara-Lombok
                            <br />
                            No Rek: 0560-676-824
                            <br />
                            a.n. Lalu Akram Wirahady
                          </p> */}
                        </Grid.Column>
                      </Grid.Row>
                      <Grid.Row>
                        <Grid.Column>Transfer Bank BRI</Grid.Column>
                        <Grid.Column>
                          <img
                            alt=""
                            width="auto"
                            height={20}
                            src="https://ecs7.tokopedia.net/img/toppay/sprites/bri.png"
                          />
                          {/* <p>
                            BRI KCP Sri Wijaya
                            <br />
                            No Rek: 2070-01-000214-56-1
                            <br />
                            a.n. CV Ares Pusaka Abadi
                          </p> */}
                        </Grid.Column>
                      </Grid.Row>
                      <Grid.Row>
                        <Grid.Column>Paypal</Grid.Column>
                        <Grid.Column>
                          <img
                            alt=""
                            width="auto"
                            height={30}
                            src="https://graffikgallery.co.uk/wp-content/uploads/2016/09/paypal-attend-graffik-gallery-corporate-graffiti-workshops.png"
                          />
                        </Grid.Column>
                      </Grid.Row>
                    </Grid>
                    <br />
                    <i>
                      <b>
                        Please check your email after submit. We will send you
                        information about the payment, and you can choose one of
                        the payment methods. Including PayPal.
                      </b>
                    </i>
                  </Card.Content>
                </Card>
                <Button positive type="button" onClick={this.bookNow}>
                  SUBMIT
                </Button>
              </Form>
              <Dimmer active={this.state.submitting}>
                <Loader size="massive">Submitting...</Loader>
              </Dimmer>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button content="Cancel" onClick={this.hideModalHotel} />
          </Modal.Actions>
        </Modal>

        {/* // MODAL SUBMIT SUCCESS // */}
        <Modal
          open={this.state.modalSubmitSuccess}
          basic
          size="small"
          onClose={() => this.setState({ modalSubmitSuccess: false })}
        >
          <Header icon="check" content="Submitted" />
          <Modal.Content>
            <p>
              Your book request is submitted! Please check your email. If it's not in your Inbox, check SPAM folder.
            </p>
          </Modal.Content>
          <Modal.Actions>
            <Button
              color="green"
              inverted
              onClick={() =>
                this.setState(
                  {
                    modalSubmitSuccess: false
                  },
                  this.hideModalHotel
                )
              }
            >
              <Icon name="checkmark" /> OK
            </Button>
          </Modal.Actions>
        </Modal>
      </React.Fragment>
    );
  }
}

